	},
	methods: {
		
	},
	onShow:function(){
		let userinfo = this.getUserInfo();
		let _uid = userinfo.id;
		uni.request({
			url: this.createApiUrl('order/get/uid'),
			method: 'GET',
			data: {uid: _uid},
			success: res => {
				res = res.data.data;
				console.log(res);
				this.rtData = res;
			},
			fail: () => {},
			complete: () => {}
		});
	}
}
